@use '../utils/variables' as *;
@use '../utils/mixins' as *;

//?Header Styles
header {
    width: 100%;
    & .topBanner {
        @include centering(column);
        @include background-style;
        position: relative;
        width: 100%;
        height: 342px;
        background-image: url('../../assets/images/contact-banner.jpg');
        object-fit: contain;
        background-attachment: fixed;
        top: -84px;
        margin-bottom: -45px;
        padding-top: 100px;
        overflow: hidden;
        overflow-anchor: none;
        z-index: 1;

        &::after {
            position: absolute;
            content: '';
            inset: 0;
            background-color: rgba(68, 68, 68, 0.5);
            z-index: 2;
        }

        & .title {
            position: absolute;
            text-align: center;
            z-index: 3;
            & h2 {
                color: $primaryText2Color;
                font: 700 $h2-fontSize-lg $secondaryFont;
            }
            & p {
                color: $primaryText2Color;
                font: 16px 'Hind';
            }
        }
    }
}

//?Contact section Styles
.contact_container {
    width: 100%;
    padding: 0 15px;
    margin: auto;

    @include custom-media(min-width, $tablet-sm) {
        display: flex;
        flex-direction: row;
    }

    @include media(tablet-lg) {
        width: 720px;
    }
    @include media(desktop-sm) {
        width: 940px;
    }
    @include media(desktop-lg) {
        width: 1200px;
    }

    & .textContent {
        margin-top: 20px;

        @include custom-media(min-width, $tablet-sm) {
            width: 50%;
            margin-right: 15px;
        }

        & .contact {
            margin-top: 25px;
            list-style: none;

            & li {
                display: flex;
                & span {
                    color: $secondaryColor;
                    margin-right: 10px;
                }

                & a {
                    text-decoration: none;
                    color: inherit;

                    &:hover {
                        text-decoration: underline;
                        color: $primaryColor;
                        font-weight: 600;
                    }
                }
            }
        }
    }

    & .contact_form {
        margin-top: 50px;

        @include custom-media(min-width, $tablet-sm) {
            width: 50%;
            margin-left: 15px;
        }
    }
}

//?Google Map Section Styles
.location_container {
    & iframe {
        width: 100%;
        height: 450px;
        border: 2px solid $secondaryColor;
    }

    margin-top: 60px;
}

//?Social icons Styles
.social_container {
    width: 100%;
    margin: 30px 0;
}
