@use '../utils/variables' as *;
@use '../utils/mixins' as *;

//Button
.btn {
    display: inline-block;
    text-align: center;
    font-family: $secondaryFont;
    text-transform: uppercase;
    text-decoration: none;
    border-style: solid;
    line-height: normal;
    border-width: 2px;
    font-weight: 400;
    white-space: nowrap;
    cursor: pointer;
    transition: 0.3s all ease;

    & a {
        text-decoration: none;
    }
}

.btn-trans-white {
    @include button-color(transparent, white, white);

    & a {
        color: white;
        &:hover {
            color: white;
        }
    }
}
.btn-trans-black {
    @include button-color(transparent, black, black);
    & a {
        color: black;
    }
}
.btn-orange {
    @include button-color($secondaryColor, white, transparent);
    & a {
        color: white;
        &:hover {
            color: white;
        }
    }
}

.btn-sm {
    @include button-size(10px, 20px, 12px);
}
.btn-md {
    @include button-size(12px, 20px, 12px);
}
.btn-lg {
    @include button-size(12px, 20px, 14px);
}
.btn-xl {
    @include button-size(10px, 30px, 14px);
}

.weight-700 {
    font-weight: 700;
}
